import React, { useState } from 'react';
import Select from 'react-select';
import axios from 'axios';


const CreateProductForm = ({
    getVendors = () => []
}) => {

    const [isAwait, setIsAwait] = useState(false);
    const [code, setCode] = useState('');
    const [vendorId, setVendorId] = useState(null);

    const [id, setId] = useState(null);


    const submit = e => {
        e.preventDefault();

        setIsAwait(true);

        axios
            .post('http://127.0.0.1:6080/api/v1/private/product/create', 
            {
                code,
                vendorId
            })
            .then(({ data = {} }) => {
                const { id = null } = data;
                setId(id);
                setCode('');
                setVendorId(null);
            })
            .finally(() => setIsAwait(false))
    };

    const getForm = () => <form onSubmit={ submit } >
        <div className="form-group">
            <label>Код товара</label>
            <input type="text"
                disabled={ isAwait }
                className="form-control"
                value={ code }
                onChange={ e => setCode(e.target.value) }
                />
        </div>

        <div className="form-group">
            <label>Производитель</label>

            <Select options={ getVendors() }
                value={ vendorId ? getVendors().find(k => k.value == vendorId) : '' }
                onChange={ v => setVendorId(parseInt(v.value)) }
                disabled={ isAwait }
            />
        </div>

        <div className="form-group">
            <button type="submit"
                    className="btn btn-info"
                    >Создать</button>
        </div>
    </form>;

    const openLink = () => <a target='_blank' 
                              href={ `http://127.0.0.1:5500/public/product-card.html?id=${ id }` }
                              className='btn btn-info' >Продолжить</a>

    return <>{ id ? openLink() : getForm() }</>;
};

export default CreateProductForm;
